import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';

export class Cart {
	totalPrice: number;
    totalQuantity: number;
    items: any[] = [];
 
	constructor() {
		this.totalPrice = 0;
        this.totalQuantity = 0;
		this.items = [];
	}
}

export class Order {
	user_id: number;
    hour: any;
    date: any;
    items: any;
    local: String;
    locality?: String;
    address?: String;
    postal_code?: String;
    payment_method_id: number;
    sub_total: number;
    total: number;
    coupon: string;
    note?: string;
    nif?: number;
    coupon_value?: number;
    fee?: number;
    change?: number;

	constructor() {
		this.payment_method_id = null;
        this.postal_code = null;
        this.hour = null;
	}
}

@Injectable()
export class CartService {
    API_URL = 'http://172.24.62.104/link.eddmi.com/index.php/takeaway/';
    ENTITY_ID = 2;
	// API_URL = 'http://localhost/quero-takeaway/index.php/';
	constructor(
		public events: Events,
		public storage: Storage,
		public http: Http
	) {}

    addItem(item: any, measure: any): Promise<Cart> {
        return this.get().then((cart) => {
            console.log(cart);
            let auxItem = cart.items.find((p: any) => p.id === item.id && p.measure_name === measure.name);
            if(!auxItem) {
                let newItem = item;
                newItem.measure_name = measure.name;
                newItem.price = measure.value;
                newItem.quantity = 1;
                cart.totalQuantity++;
                cart.items.push(newItem);
            }
            else {
                var index = cart.items.indexOf(auxItem);
                cart.items[index].quantity++;
            }
            cart.totalPrice += Number(measure.value);
            cart.totalPrice = Number(cart.totalPrice.toFixed(2));
            this.set(cart);
            return cart;
        })
    }

    incrementQuantity(item: any): Promise<Cart> {
        return this.get().then((cart) => {
            console.log(cart);
            let auxItem = cart.items.find((p: any) => p.id === item.id && p.measure_name === item.measure_name);
            var index = cart.items.indexOf(auxItem);
            cart.items[index].quantity++;
            cart.totalPrice += Number(cart.items[index].price);
            cart.totalPrice = Number(cart.totalPrice.toFixed(2));
            this.set(cart);
            return cart;
        })
    }

    decrementQuantity(item: any): Promise<Cart> {
        return this.get().then((cart) => {
            let auxItem = cart.items.find((p: any) => p.id === item.id && p.measure_name === item.measure_name);
            var index = cart.items.indexOf(auxItem);
            if(cart.items[index].quantity > 1) {
                cart.items[index].quantity--;
                cart.totalPrice -= Number(cart.items[index].price);
                cart.totalPrice = Number(cart.totalPrice.toFixed(2));
                this.set(cart);
            }
            return cart;
        })
    }

    removeItem(item: any): Promise<Cart> {
        return this.get().then((cart) => {
            let auxItem = cart.items.find((p: any) => p.id === item.id && p.measure_name === item.measure_name);
            var index = cart.items.indexOf(auxItem);
            if (index > -1) {
                cart.totalQuantity--;
                cart.totalPrice -= auxItem.quantity * Number(auxItem.price);
                cart.totalPrice = Number(cart.totalPrice.toFixed(2));
                cart.items.splice(index, 1);
            }
            this.set(cart);
            return cart;
        })
    }

	set(cart: Cart): void {
		this.storage.set('cart', cart);
	};

	get(): Promise<Cart> {
		return this.storage.get('cart').then((value) => {
            if(value) {
                return value;
            }
            else {
                let cart = new Cart();
                this.set(cart);
                return cart;
            }
		});
	};

    paymentMethods() : Promise<any> {
		return new Promise(resolve => {
			var url = this.API_URL + '/tkpaymentMethod/all';
			let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
			let _options = new RequestOptions({headers: _headers});
			this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					resolve(data);
				});
		});
	}  
}



