import { Injectable } from '@angular/core';

import { Http, Headers, RequestOptions } from '@angular/http';

import { UserService } from './user-service';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/of';

@Injectable()
export class OrderService {
	API_URL = 'http://172.24.62.104/link.eddmi.com/index.php/takeaway/';
	// API_URL = 'http://localhost/quero-takeaway/index.php/';
	orders: any;
	constructor(public http: Http, public userService: UserService) {
		console.log('Hello OrderService Provider');
	}

	all(): Promise<any> {
		return new Promise((resolve, reject) => {
			this.userService.getToken().then(data => {
				let _headers = new Headers({ 'Authorization': data });
				let _options = new RequestOptions({headers: _headers});
				let url = this.API_URL + '/tkorder/all/';
				this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					this.orders = data.data.orders;
					resolve(this.orders);
				}, error => {
					reject(error);
				});
			});	
		});
	}  

	create(order: any): Promise<any> {
		return new Promise((resolve, reject) => {
			this.userService.getToken().then(data => {
				let _headers = new Headers({ 'Authorization': data });
				let _options = new RequestOptions({headers: _headers});
				var url = this.API_URL  + 'tkorder/new';
				this.http.post(url, order, _options)
				.map(res => res.json())
				.subscribe(data => {
					console.log(data);
					resolve(data);
				}, error => {
					console.log(error);
					reject(error);
				});
			});
		});
	};  
}



