import { Component, ViewChild } from '@angular/core';

import { IonicPage, AlertController, App, FabContainer, ItemSliding, List, ModalController, NavController, ToastController, LoadingController, Refresher } from 'ionic-angular';

/*
	To learn how to use third party libs in an
	Ionic app check out our docs here: http://ionicframework.com/docs/v2/resources/third-party-libs/
*/
// import moment from 'moment';

import { ProductService } from '../../providers/menu-service';
import { UserService } from '../../providers/user-service';
import { Order, Cart, CartService } from '../../providers/cart-service';
import { OrderService } from '../../providers/order-service';

@IonicPage()
@Component({
	selector: 'page-order',
	templateUrl: 'order.html',
	providers: [ProductService, CartService, OrderService]
})
export class OrderPage {
	// the list is a child of the menu page
	// @ViewChild('scheduleList') gets a reference to the list
	// with the variable #scheduleList, `read: List` tells it to return
	// the List and not a reference to the element

	segment = 'pending';

	fixedMenu: any = [];
	dailyMenu: any = [];
	orders: any;
	pending: any;
	loading: any;

	constructor(
		public alertCtrl: AlertController,
		public app: App,
		public loadingCtrl: LoadingController,
		public modalCtrl: ModalController,
		public navCtrl: NavController,
		public toastCtrl: ToastController,
		public productService: ProductService,
		public orderService: OrderService,
		public user: UserService
	) {

	}

	ionViewDidLoad() {
		this.app.setTitle('orders');
		// this.cartService.set(this.cart);
		this.loadOrders();
	}

	loadOrders(){
		this.loading = this.loadingCtrl.create();
		this.loading.present().then(()=>{
			this.orderService.all()
			.then(data => {
				this.orders = data;
				this.pending = data.filter((d: any) => d.state == 'Submetida' || d.state == 'Em preparação' || d.state == 'Em processo de entrega');
				this.loading.dismiss();
			}, error => {
				this.loading.dismiss();
			});
				
		})
	}

	detail(order: Order) {
		this.navCtrl.push('OrderDetailPage', {
			order: order
		});
	}
}



